/** @format */
* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  cursor: url(data:image/png;base64,), auto;
}
/* Hide scrollbar for Chrome, Safari and Opera */
/* width */
::-webkit-scrollbar {
  width: 0px;
  height: 0;
}

/* Track */
::-webkit-scrollbar-track {
  background: #14343f;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffc500;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/** @format Nabvar*/

.header-navbar {
  top: 0;
  height: 64px;
  -webkit-align-items: center;
          align-items: center;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  z-index: 100;
}
/*Propiedades de los containers*/
.header-navbar .navbarDiv1 {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  outline: none;
}
.header-navbar .navbarDiv2 {
  width: 33.33%;
  float: left;

  overflow: hidden;
  height: 64px;
  padding: auto;
}

.header-navbar .navbarDiv3 {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;
}

.header-navbar .borderClick {
  width: 0px;
  height: 0px;
}
.header-navbar .navbarDiv3 .borderClick {
  width: 0px;
  height: 0px;
  float: right;
  margin-right: 110px;
}
@media only screen and (min-width: 600px) {
  .header-navbar {
  }
  .header-navbar .navbarDiv2 .borderClick {
    margin-left: 30%;
    margin-right: 0;
  }

  .header-navbar .navbarDiv2 {
    width: 200px;
    position: absolute;
    right: 70px;
  }
  .header-navbar .navbarDiv15 {
    width: 160px;
    position: absolute;
    right: 280px;

    height: 64px;
    padding: auto;
  }
  .header-navbar .navbarDiv15 .resources-div {
    margin-top: 15px;
  }
  .header-navbar .navbarDiv3 {
    width: 60px;
    display: block;

    position: absolute;
    right: 0;
  }
  /*BOTON en modo desktop*/
  .header-navbar .navbarDiv2 .borderClick {
    float: right;
    margin-right: 150px;
  }
}

/** @format */

.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: 14px;
  margin-right: auto;
  background-color: transparent;
}

.button-hamburgerIcon .img-hamburger {
  height: 32px;
  vertical-align: middle;
}

.button-hamburgerIcon:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

/*icon pregunta*/
@media only screen and (min-width: 600px) {
  .button-hamburgerIcon {
    margin-left: auto;
    margin-right: 14px;
  }
  .div-menu {
    margin-left: calc(-100vw + 100%);
  }
}

/** @format */
:root {
  --azul: #14343f;
  --azulClaro: #14343f;
  --azulObscuro: #153540;
}
.div-movile {
  margin-top: 18px;
  background-color: #14343f;
  background-color: var(--azulClaro);
  width: 100vw;
  margin-left: calc(+100vw - 300%);
  overflow: hidden;
  font-family: "Noto Sans JP", sans-serif;
  border-radius: 0px 0px 10px 10px;
}

.div-movile .li {
  text-decoration: none;
  display: inline-block;
  width: 100vw;
  margin-right: calc(-100vw + 100%);
  padding: 30px 32px 32px 20px;
  font-size: 20px;
  letter-spacing: 3.5px;

  text-align: justify;
}
.div-movile .li-start {
  width: 100%;
  padding: 30px 32px 32px 0px;
}
.div-movile .li-start .button {
  margin-left: 20px;
}
.div-movile .li-start:hover {
  background-color: #2e4a58;
  transition: background-color 100ms linear;
}

.div-movile .li:hover {
  cursor: pointer;
  -webkit-text-decoration: underline #fff;
          text-decoration: underline #fff;
  background-color: #2e4a58;
  cursor: url(data:image/png;base64,), auto;
  transition: background-color 100ms linear;
}
/*Icons*/
.div-movile .li .icons-menu {
  margin-bottom: -5px;
  margin-right: 5px;
  margin-left: 0px;
  color: #909090;
}
.div-movile .li .icons-menu-cloud {
  margin-bottom: -4px;
  margin-right: 7px;
  margin-left: 0px;
  color: #909090;
}
@media only screen and (min-width: 600px) {
  .div-movile {
    margin-top: 14px;
    background-color: #202020de;
    width: 300px;
    margin-left: -403%;
    overflow: hidden;
    font-family: "Roboto", sans-serif;
    border-radius: 0;
    border: solid 0px;
    border-color: #909090;
    border-top: 0;
  }
  .div-movile .li {
    font-size: 17px;
  }
  .div-movile .li:hover {
    cursor: pointer;
    -webkit-text-decoration: underline #909090;
            text-decoration: underline #909090;
    background-color: #373737;
    cursor: url(data:image/png;base64,), auto;
    transition: background-color 100ms linear;
  }
  .div-movile .li-start:hover {
    background-color: #373737;
    transition: background-color 100ms linear;
  }
  .div-movile .li-start .button {
    margin-left: 30px;
  }
}

/** @format */
.div-ButtonStart {
  width: 120px;
  height: 45px;
}
.ButtonStart {
  outline: none;
  background-color: #ffc500;
  color: #fff;
  /*Size*/
  border-radius: 5px;
  font-size: large;
  font-family: "Press Start 2P";
  letter-spacing: 1.5px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: -3px 5px #c6571b;
  border: none;
  cursor: pointer;
  float: right;
  /*Margin*/
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
}

.ButtonStart:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition-duration: 0.03s;
}
.ButtonStart:hover {
  background-color: #fdd44d;
  cursor: url(data:image/png;base64,), auto;
}

